.newsletter .center {
  height: 90px;
  border-top: 3px solid gainsboro;
  border-bottom: 3px solid gainsboro;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.newsletter .center i {
  font-size: 35px;
}

.newsletter .center p {
  margin-left: 30px;
  width: 400px;
  border-right: 3px solid gainsboro;
}

.newsletter .center .input-area {
  background: chartreuse;
  margin-left: 30px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-radius: 15px;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  overflow: hidden;
}

.newsletter .center .input-area input {
  height: 40px;
  width: 200px;
  border: none;
  outline: none;
  color: #312d2d;
  padding-left: 10px;
  padding-right: 10px;
  background: gainsboro;
}

.newsletter .center .input-area button {
  height: 40px;
  background: #312d2d;
  color: white;
  border: none;
  padding-left: 15px;
  padding-right: 15px;
  cursor: pointer;
}

.newsletter .center .input-area button:hover {
  background: #747373;
}

@media screen and (max-width: 768px) {
  .newsletter .center {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    height: auto;
  }
  .newsletter .center i {
    margin-top: 20px;
  }
  .newsletter .center p {
    border: none;
    width: 300px;
  }
  .newsletter .center .input-area {
    margin-top: 20px;
    margin-bottom: 20px;
  }
}
