.grid {
  margin-top: 50px;
}

.grid .center {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: (1fr)[4];
      grid-template-columns: repeat(4, 1fr);
  -ms-grid-rows: (1fr)[2];
      grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 10px;
  grid-row-gap: 10px;
}

.grid .grid-container {
  overflow: hidden;
  height: 300px;
  position: relative;
}

.grid .grid-container:hover .image {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.grid .grid-container .text {
  z-index: 9;
  position: absolute;
  color: white;
  bottom: 5px;
  font-size: 15px;
}

.grid .grid-container .text button {
  border: none;
  background: transparent;
  border-bottom: 3px solid white;
  color: white;
  margin-top: 10px;
  font-size: 16px;
  cursor: pointer;
}

.grid .grid-container .image {
  height: 100%;
  width: 100%;
  background-position: center;
  background-size: cover;
  -webkit-transition: all ease 0.3s;
  transition: all ease 0.3s;
}

.grid .grid-container .image .fade-bg {
  background: -webkit-gradient(linear, left bottom, left top, color-stop(-45%, #312d2d), to(transparent));
  background: linear-gradient(to top, #312d2d -45%, transparent);
  height: 100%;
}

.item1 {
  -ms-grid-row: 1;
  -ms-grid-row-span: 1;
  -ms-grid-column: 1;
  -ms-grid-column-span: 3;
  grid-area: 1 / 1 / 2 / 4;
}

.item2 {
  -ms-grid-row: 1;
  -ms-grid-row-span: 1;
  -ms-grid-column: 4;
  -ms-grid-column-span: 1;
  grid-area: 1 / 4 / 2 / 5;
}

.item3 {
  -ms-grid-row: 2;
  -ms-grid-row-span: 1;
  -ms-grid-column: 2;
  -ms-grid-column-span: 3;
  grid-area: 2 / 2 / 3 / 5;
}

.item4 {
  -ms-grid-row: 2;
  -ms-grid-row-span: 1;
  -ms-grid-column: 1;
  -ms-grid-column-span: 1;
  grid-area: 2 / 1 / 3 / 2;
}

@media screen and (max-width: 768px) {
  .grid .center {
    -ms-grid-columns: 1fr;
        grid-template-columns: 1fr;
    -ms-grid-rows: (1fr)[4];
        grid-template-rows: repeat(4, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 10px;
  }
  .grid-container .text {
    left: 10px;
  }
  .item1 {
    -ms-grid-row: 1;
    -ms-grid-row-span: 1;
    -ms-grid-column: 1;
    -ms-grid-column-span: 1;
    grid-area: 1 / 1 / 2 / 2;
  }
  .item2 {
    -ms-grid-row: 2;
    -ms-grid-row-span: 1;
    -ms-grid-column: 1;
    -ms-grid-column-span: 1;
    grid-area: 2 / 1 / 3 / 2;
  }
  .item3 {
    -ms-grid-row: 4;
    -ms-grid-row-span: 1;
    -ms-grid-column: 1;
    -ms-grid-column-span: 1;
    grid-area: 4 / 1 / 5 / 2;
  }
  .item4 {
    -ms-grid-row: 3;
    -ms-grid-row-span: 1;
    -ms-grid-column: 1;
    -ms-grid-column-span: 1;
    grid-area: 3 / 1 / 4 / 2;
  }
}
