$font-theme: rgb(49, 45, 45);

.shop-grid{

    .load-button{
        border-radius: 10px;
        background: $font-theme;
        border: none;
        color: white;
        display: block;
        font-size: 18px;
        padding: 10px 15px;
        cursor: pointer;
        margin: 20px auto;
        
        transition: all ease 0.3s;

        i{
            animation: spin 2s linear infinite;
        }

        &:hover{
            background: rgb(116, 115, 115);
        }
    }

    margin-top: 50px;
    
    .tabs{
        display: flex;
        justify-content: center;
        gap: 20px;
        font-weight: bold;
        font-size: 15px;

        .active{
            border-bottom: 3px solid #f49a59;
        }
    }

    .shop{
        display: flex;
        justify-content: space-evenly;
        flex-wrap: wrap;

        .shop-container{
            margin-top: 30px;
            width: 270px;
            height: 450px;
            position: relative;
            overflow: hidden;
            cursor: pointer;

            &:hover{
                .interact{

                opacity: 1;
                animation: topa;
                animation-duration: 0.5s;
                }
            }
            
            .interact{
                position: absolute;
                display: flex;
                flex-direction: column;
                opacity: 0;
                gap: 10px;
                left: 10px;
                top: 10px;
                

                 button{
                     border: none;
                     width: 40px;
                     display: flex;
                     justify-content: center;
                     align-items: center;
                     height: 40px;
                     color: white;
                     font-size: 18px;
                     border-radius: 20px;
                     background: #f49a59;
                     cursor: pointer;
                     transition: all ease 0.3s;

                     &:hover{
                        background: #f5c4a2
                     }

                 }
            }

            .image{
                background-position: center;
                background-size: cover;
                width: 100%;
                height: 80%;
               
            }

            .rate{
                margin-top: 5px;
                color: $font-theme;
                opacity: 0.5;

                i{
                    color: gold;
                }
                
            }

            h1{
                font-size: 13px;
                margin-top: 5px;
            }

            p{
                margin-top: 5px;
                font-size: 20px;
            }
        }
    }
}

@keyframes topa{
    from{
        left: -50px;
    }
    to{
        left: 10px;
    }
}

@keyframes spin{
    100%{
        transform: rotate(360deg);
    }
}