.pre-footer .center {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 70px;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.pre-footer .center .shop-info, .pre-footer .center .social-media {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 20px;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  list-style-type: none;
  font-weight: bold;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  font-size: 14px;
}

.pre-footer .center .shop-info a, .pre-footer .center .social-media a {
  color: #312d2d;
  text-decoration: none;
}

.pre-footer .center .social-media {
  font-size: 18px;
}

@media screen and (max-width: 768px) {
  .pre-footer {
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .pre-footer .center {
    height: auto;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    gap: 20px;
  }
}
