$font-theme: rgb(49, 45, 45);


.info{

    .center{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    }
}

.container{
    width: 300px;
    margin-top: 30px;
    height: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 15px;
    background: rgba(0, 0, 0, 0.05);
    

    h1{
        font-size: 25px;
    }

    p{
        font-size: 15px;
        
    }

    i{
        color: #f49a59;
    }
}