.shop-grid {
  margin-top: 50px;
}

.shop-grid .load-button {
  border-radius: 10px;
  background: #312d2d;
  border: none;
  color: white;
  display: block;
  font-size: 18px;
  padding: 10px 15px;
  cursor: pointer;
  margin: 20px auto;
  -webkit-transition: all ease 0.3s;
  transition: all ease 0.3s;
}

.shop-grid .load-button i {
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
}

.shop-grid .load-button:hover {
  background: #747373;
}

.shop-grid .tabs {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  gap: 20px;
  font-weight: bold;
  font-size: 15px;
}

.shop-grid .tabs .active {
  border-bottom: 3px solid #f49a59;
}

.shop-grid .shop {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: space-evenly;
      -ms-flex-pack: space-evenly;
          justify-content: space-evenly;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.shop-grid .shop .shop-container {
  margin-top: 30px;
  width: 270px;
  height: 450px;
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

.shop-grid .shop .shop-container:hover .interact {
  opacity: 1;
  -webkit-animation: topa;
          animation: topa;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}

.shop-grid .shop .shop-container .interact {
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  opacity: 0;
  gap: 10px;
  left: 10px;
  top: 10px;
}

.shop-grid .shop .shop-container .interact button {
  border: none;
  width: 40px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 40px;
  color: white;
  font-size: 18px;
  border-radius: 20px;
  background: #f49a59;
  cursor: pointer;
  -webkit-transition: all ease 0.3s;
  transition: all ease 0.3s;
}

.shop-grid .shop .shop-container .interact button:hover {
  background: #f5c4a2;
}

.shop-grid .shop .shop-container .image {
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 80%;
}

.shop-grid .shop .shop-container .rate {
  margin-top: 5px;
  color: #312d2d;
  opacity: 0.5;
}

.shop-grid .shop .shop-container .rate i {
  color: gold;
}

.shop-grid .shop .shop-container h1 {
  font-size: 13px;
  margin-top: 5px;
}

.shop-grid .shop .shop-container p {
  margin-top: 5px;
  font-size: 20px;
}

@-webkit-keyframes topa {
  from {
    left: -50px;
  }
  to {
    left: 10px;
  }
}

@keyframes topa {
  from {
    left: -50px;
  }
  to {
    left: 10px;
  }
}

@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
