.banner {
  padding-top: 90px;
}

.image-box {
  width: 400px;
  height: 85vh;
  background-position: center;
  background-size: cover;
}

.featured {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.featured .content {
  margin-left: 30px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  gap: 30px;
}

.featured .content h1 {
  font-size: 16px;
  border-left: 3px solid #312d2d;
  padding-left: 10px;
}

.featured .content p {
  font-size: 40px;
}

.featured .content .image-grid {
  position: absolute;
  bottom: 0;
  border-top: 3px solid #312d2d;
  padding-top: 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 40vw;
  gap: 10px;
}

.featured .content .image-grid .image {
  width: 130px;
  height: 12vw;
  -webkit-transition: all ease-out 0.3s;
  transition: all ease-out 0.3s;
  background-size: cover;
  cursor: pointer;
  background-position: center;
}

.featured .content .image-grid .image:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.featured button {
  background: #312d2d;
  color: white;
  border: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-weight: bold;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 15px 15px 10px 10px;
  border: 2px solid transparent;
  cursor: pointer;
  -webkit-transition: all ease 0.2s;
  transition: all ease 0.2s;
}

.featured button:hover {
  background: whitesmoke;
  border: 2px solid #312d2d;
  border-radius: 15px;
  color: #312d2d;
}

.featured-mobile {
  display: none;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  text-align: center;
  background-size: cover;
  background-position: top;
  height: 70vh;
  position: relative;
  overflow: hidden;
  margin-bottom: 20px;
}

.featured-mobile::before {
  content: "";
  width: 100%;
  height: 100%;
  background: -webkit-gradient(linear, left bottom, left top, from(#c4c3c3), to(transparent));
  background: linear-gradient(to top, #c4c3c3, transparent);
  z-index: 2;
}

.featured-mobile p {
  font-size: xx-large;
}

.featured-mobile .content {
  position: absolute;
  width: 100%;
  bottom: 0;
  z-index: 3;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.featured-mobile button {
  background: #312d2d;
  color: white;
  border: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  font-weight: bold;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 15px 15px 10px 10px;
  border: 2px solid transparent;
  cursor: pointer;
  -webkit-transition: all ease 0.2s;
  transition: all ease 0.2s;
}

.featured-mobile button:hover {
  background: whitesmoke;
  border: 2px solid #312d2d;
  border-radius: 15px;
  color: #312d2d;
}

@media screen and (max-width: 768px) {
  .image-box {
    display: none;
  }
  .featured {
    display: none;
  }
  .featured-mobile {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
  }
}
