.banner{

    padding-top: 90px;
}

$font-theme: rgb(49, 45, 45);

.image-box{
    width: 400px;
    height: 85vh;
    background-position: center;
    background-size: cover;

}

.featured{
    display: flex;

    .content{
        margin-left: 30px;
        display: flex;
        position: relative;
        flex-direction: column;
        gap: 30px;

        h1{
            font-size: 16px;
            border-left: 3px solid $font-theme;
            padding-left: 10px;
            
        }

        p{
            font-size: 40px;
        }

        .image-grid{
            position: absolute;
            bottom: 0;
            border-top: 3px solid $font-theme;
            padding-top: 10px;
            display: flex;
            width: 40vw;
            gap: 10px;

            .image{
              width: 130px;
                height: 12vw;
                transition: all ease-out 0.3s;
                background-size: cover;
                cursor: pointer;
                background-position: center;

                &:hover{
                    transform: scale(1.1);
                }
            }
        }
    }

    button{
  
        background: $font-theme;
        color: white;
        border: none;
        display: flex;
        font-weight: bold;
        justify-content: center;
        align-items: center;
        padding: 15px 15px 10px 10px;
        border: 2px solid transparent;
        cursor: pointer;
        transition: all ease 0.2s;

        &:hover{
            background: whitesmoke;
            border: 2px solid $font-theme;
            border-radius: 15px;
            color: $font-theme;
        }
    }
}

.featured-mobile{
    display: none;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    background-size: cover;
    background-position: top;
    height: 70vh;
    position: relative;
    overflow: hidden;
    margin-bottom: 20px;

    &::before{
        content: "";
        width: 100%;
        height: 100%;
        background: linear-gradient(to top, rgb(196, 195, 195), transparent);
        z-index: 2;
    }

    p{
        font-size: xx-large;
    }

    .content{
        position: absolute;
        width: 100%;
        bottom: 0;
        z-index: 3;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    button{
        background: $font-theme;
        color: white;
        border: none;
        display: flex;
        width: 100%;
        font-weight: bold;
        justify-content: center;
        align-items: center;
        padding: 15px 15px 10px 10px;
        border: 2px solid transparent;
        cursor: pointer;
        transition: all ease 0.2s;

        &:hover{
            background: whitesmoke;
            border: 2px solid $font-theme;
            border-radius: 15px;
            color: $font-theme;
        }
    }
}

@media screen and (max-width: 768px){
    .image-box{
        display: none;
    }

    .featured{
        display: none;
    }

    .featured-mobile{
        display: flex;
    }
}