$font-theme: rgb(49, 45, 45);

.pre-footer .center{
    display: flex;
    align-items: center;
    height: 70px;
    justify-content: space-between;

    .shop-info, .social-media{
        display: flex;
        gap: 20px;
        flex-wrap: wrap;
        list-style-type: none;
        font-weight: bold;
        justify-content: center;
        font-size: 14px;

        a{
            color: $font-theme;
            text-decoration: none;
        }
    }

    .social-media{
        font-size: 18px;
    }

}

@media screen and (max-width: 768px){

    .pre-footer{
        margin-top: 20px;
        margin-bottom: 20px;
    }
    .pre-footer .center{
        height: auto;
        flex-direction: column;
        gap: 20px;
    }
}

