$font-theme: rgb(49, 45, 45);

header{
    height: 60px;
    display: flex;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 10;
    background-color: #f5f5f5;
    align-items: center;

    .center{
        display: flex;
    }
}

.menu-mboile{
    display: none;
}

.logo{
    width: 80px;
    margin-top: -5px;
}

.user-navigation{
    position: absolute;
    right: 0;
    top: 10px;

    ul{
        display: flex;
        list-style-type: none;
        gap: 10px;

        a{
            text-decoration: none;
            color: $font-theme;
        }

      
    }
}

.shop-navigation{
    display: flex;
    align-self: center;
    margin-left: 60px;

    ul{
        display: flex;
        list-style-type: none;
        gap: 10px;
        font-weight: bold;

        li{
           cursor: pointer;

            i{
                font-size: 10px;
            }
        }
    }
}

.search-bar{

    width: 50%;
    
    display: none;
    position: relative;
    align-self: center;
    margin: 0 auto;
   
    

    i{
        position: absolute;
        display: flex;
        align-self: center;
        left: 10px;
    }
    
    input{
        width:100%;
        height: 30px;
        border: none;
        outline: none;
        padding-left: 35px;
        padding-right: 10px;
        border-radius: 20px;
    }
}

.menu-mobile{
    display: none;
    position: absolute;
    right: 0;
    border: none;
    background: transparent;
    align-self: center;
    font-size: 25px;
}

@media screen and (max-width: 768px){
    .shop-navigation, .user-navigation{
        display: none;
    }

    .menu-mobile, .search-bar{
        display: flex;
    }
    
}