$font-theme: rgb(49, 45, 45);


.grid {
    margin-top: 50px;
.center{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 10px;
    grid-row-gap: 10px;

    

    }

    .grid-container{
        overflow: hidden;
        height: 300px;
        position: relative;

        &:hover .image{
            transform: scale(1.1);
            
        }

        .text{
            z-index: 9;
            position: absolute;
            color: white;
            bottom: 5px;
            font-size: 15px;

           
         
            button{
                border: none;
                background: transparent;
                border-bottom: 3px solid white;
                color: white;
                margin-top: 10px;
                font-size: 16px;
                cursor: pointer;
            }
     
        }

        .image{
            height: 100%;
            width: 100%;
            background-position: center;
            background-size: cover;
            transition: all ease 0.3s;

            

           .fade-bg{
               background: linear-gradient(to top, $font-theme -45%, transparent);
               height: 100%;
           }
            
            
        }

       
       

    }

}
    
.item1 { grid-area: 1 / 1 / 2 / 4; }
.item2 { grid-area: 1 / 4 / 2 / 5; }
.item3 { grid-area: 2 / 2 / 3 / 5; }
.item4 { grid-area: 2 / 1 / 3 / 2; }


@media screen and (max-width: 768px){
    .grid .center{
        grid-template-columns: 1fr;
        grid-template-rows: repeat(4, 1fr);
        grid-column-gap: 0px;
        grid-row-gap: 10px;
    }

    .grid-container{
       
        .text{
            left: 10px;
        }
    }

.item1 { grid-area: 1 / 1 / 2 / 2; }
.item2 { grid-area: 2 / 1 / 3 / 2; }
.item3 { grid-area: 4 / 1 / 5 / 2;  }
.item4 { grid-area: 3 / 1 / 4 / 2; }
}
