$font-theme: rgb(49, 45, 45);

footer {
    background: $font-theme;
.center{
    display: flex;
    flex-wrap: wrap;
    padding-top: 10px;
    padding-bottom: 10px;
    align-items: center;
    justify-content: space-between;

    p{
        color: white;
        font-size: 11px;
        width: 300px;

    }

    .payments{
        display: flex;
        color: white;
        justify-content: space-around;
        flex-wrap: wrap;
        align-items: center;
        font-size: 50px;
        gap: 20px;
    
    }
}

}

@media screen and (max-width: 862px){
    footer .center{
        flex-direction: column;
        gap: 20px;
        margin: 0 auto;
        justify-content: center;
    }

    p{
        padding-top: 30px;
    }
}