header {
  height: 60px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10;
  background-color: #f5f5f5;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

header .center {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.menu-mboile {
  display: none;
}

.logo {
  width: 80px;
  margin-top: -5px;
}

.user-navigation {
  position: absolute;
  right: 0;
  top: 10px;
}

.user-navigation ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  list-style-type: none;
  gap: 10px;
}

.user-navigation ul a {
  text-decoration: none;
  color: #312d2d;
}

.shop-navigation {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-item-align: center;
      align-self: center;
  margin-left: 60px;
}

.shop-navigation ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  list-style-type: none;
  gap: 10px;
  font-weight: bold;
}

.shop-navigation ul li {
  cursor: pointer;
}

.shop-navigation ul li i {
  font-size: 10px;
}

.search-bar {
  width: 50%;
  display: none;
  position: relative;
  -ms-flex-item-align: center;
      -ms-grid-row-align: center;
      align-self: center;
  margin: 0 auto;
}

.search-bar i {
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-item-align: center;
      align-self: center;
  left: 10px;
}

.search-bar input {
  width: 100%;
  height: 30px;
  border: none;
  outline: none;
  padding-left: 35px;
  padding-right: 10px;
  border-radius: 20px;
}

.menu-mobile {
  display: none;
  position: absolute;
  right: 0;
  border: none;
  background: transparent;
  -ms-flex-item-align: center;
      -ms-grid-row-align: center;
      align-self: center;
  font-size: 25px;
}

@media screen and (max-width: 768px) {
  .shop-navigation, .user-navigation {
    display: none;
  }
  .menu-mobile, .search-bar {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
  }
}
