$font-theme: rgb(49, 45, 45);



.newsletter .center{
    height: 90px;
    border-top: 3px solid gainsboro;
    border-bottom: 3px solid gainsboro;
    display: flex;
    justify-content: center;
    align-items: center;

    i{
        font-size: 35px;
     
    }

    p{
        margin-left: 30px;
        width: 400px;
     
        border-right: 3px solid gainsboro;
    }

    .input-area{
     background: chartreuse;
     margin-left: 30px;
     display: flex;
     border-radius: 15px;
     flex-wrap: nowrap;
     overflow: hidden;
     
        input{
            
            height: 40px;
            width: 200px;
            border: none;
            outline: none;
            color: $font-theme;
            padding-left: 10px;
            padding-right: 10px;
            background: gainsboro;
       
        }

        button{
            height: 40px;
            background: $font-theme;
            color: white;
            border: none;
            padding-left: 15px;
            padding-right: 15px;
            cursor: pointer;

            &:hover{
                background: rgb(116, 115, 115);
            }
        }
    }
}

@media screen and (max-width: 768px){

    .newsletter .center{
        flex-direction: column;
        height: auto;

        i{
            margin-top: 20px;
        }
        

        p{
            border: none;
           width: 300px;
        }

        .input-area{
            margin-top: 20px;
            margin-bottom: 20px;
        }
    }
}